@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto/robotolight/robotolight.woff") format("woff"), url("../fonts/roboto/robotolight/robotolight.woff2") format("woff2"), url("../fonts/roboto/robotolight/robotolight.ttf") format("truetype");
  font-style: normal;
  font-weight: 300;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto/robotoregular/robotoregular.woff") format("woff"), url("../fonts/roboto/robotoregular/robotoregular.woff2") format("woff2"), url("../fonts/roboto/robotoregular/robotoregular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto/robotomedium/robotomedium.woff") format("woff"), url("../fonts/roboto/robotomedium/robotomedium.woff2") format("woff2"), url("../fonts/roboto/robotomedium/robotomedium.ttf") format("truetype");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto/robotobold/robotobold.woff") format("woff"), url("../fonts/roboto/robotobold/robotobold.woff2") format("woff2"), url("../fonts/roboto/robotobold/robotobold.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto/robotoblack/robotoblack.woff") format("woff"), url("../fonts/roboto/robotoblack/robotoblack.woff2") format("woff2"), url("../fonts/roboto/robotoblack/robotoblack.ttf") format("truetype");
  font-style: normal;
  font-weight: 900;
  font-display: swap;
}
@font-face {
  font-family: "Actor";
  src: url("../fonts/actor/actor.woff") format("woff"), url("../fonts/actor/actor.woff2") format("woff2"), url("../fonts/actor/actor.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

button,
select {
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  word-wrap: normal;
  white-space: nowrap;
  border: 0;
}

.container {
  width: 1440px;
  padding: 0 60px;
  margin: 0 auto;
}

@media (max-width: 1439px) {
  .container {
    width: 100%;
    padding: 0 64px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 100%;
    padding: 0 20px;
  }
}
@media (max-width: 398px) {
  .container {
    width: 100%;
    padding: 0 20px;
  }
}
.button {
  position: relative;
  display: inline-block;
  padding: 14px 0;
  padding-right: 40px;
  padding-left: 64px;
  margin: 0;
  color: #ffffff;
  text-decoration: none;
  background-color: #da498c;
  border: none;
  border-radius: 8px;
  transition: 300ms;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}
.button::before {
  position: absolute;
  top: 10px;
  left: 13%;
  width: 22px;
  height: 22px;
  content: "";
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.button:hover, .button:focus {
  background-color: rgba(218, 73, 140, 0.85);
}
.button:active {
  background-color: rgba(218, 73, 140, 0.3);
}

.title {
  margin: 0;
  color: rgba(0, 0, 0, 0.7);
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 900;
  line-height: 32px;
}

.logo {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  width: 230px;
  height: 35px;
}
.logo:hover .logo__1st-box, .logo:focus .logo__1st-box {
  animation: logo-1st-box 1s linear infinite alternate;
}
.logo:hover .logo__2nd-box, .logo:focus .logo__2nd-box {
  animation: logo-2nd-box 1s linear infinite alternate;
}
.logo:hover .logo__3rd-box, .logo:focus .logo__3rd-box {
  animation: logo-3rd-box 1s linear infinite alternate;
}
@media (max-width: 767px) {
  .logo {
    width: 175px;
    height: 30px;
  }
}

.logo__icon {
  width: 32px;
  height: 35px;
}
@media (max-width: 767px) {
  .logo__icon {
    width: 27px;
    height: 30px;
  }
}

.logo__text {
  width: 182px;
  height: 20px;
  margin-left: auto;
  opacity: 0.9;
}
.logo__text:hover, .logo__text:focus {
  opacity: 70%;
}
.logo__text:active {
  opacity: 50%;
}
@media (max-width: 767px) {
  .logo__text {
    width: 139px;
    height: 15px;
  }
}

@keyframes logo-1st-box {
  50% {
    fill: url("#paint1_linear");
  }
  100% {
    fill: url("#paint2_linear");
  }
}
@keyframes logo-2nd-box {
  50% {
    fill: url("#paint2_linear");
  }
  100% {
    fill: url("#paint0_linear");
  }
}
@keyframes logo-3rd-box {
  50% {
    fill: url("#paint0_linear");
  }
  100% {
    fill: url("#paint1_linear");
  }
}
.page {
  position: relative;
  height: 100%;
}

.page__body {
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  color: #000000;
  background-color: #ffffff;
}

.page__header {
  position: relative;
  z-index: 5;
  flex-shrink: 0;
  background: transparent;
}

.page__main {
  flex-grow: 1;
}

.page__footer {
  flex-shrink: 0;
}

.page-header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 35px;
  padding-top: 39px;
}
@media (max-width: 1439px) {
  .page-header__top {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 1439px) {
  .page-header__top {
    padding-top: 20px;
  }
}

@media (max-width: 1439px) {
  .page-header__top-wrap {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}

.page-header__logo {
  margin-right: 227px;
}
@media (max-width: 1439px) {
  .page-header__logo {
    margin: 0;
  }
}

.page-header__burger {
  display: none;
}

.page-header__bottom {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 10px;
}
@media (max-width: 1439px) {
  .page-header__bottom {
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .page-header__bottom {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.page-header__search {
  align-self: center;
  margin-left: 4px;
}
@media (max-width: 1439px) {
  .page-header__search {
    width: 100%;
    margin: 0;
  }
}
@media (max-width: 1439px) {
  .page-header__search {
    order: 2;
  }
}

@media (max-width: 1439px) {
  .burger {
    position: relative;
    display: block;
    width: 30px;
    height: 35px;
    padding: 0;
    background-color: transparent;
    border: none;
    transition: 200ms;
  }
  .burger:hover, .burger:focus {
    opacity: 0.6;
  }
  .burger:active {
    opacity: 0.4;
  }
  .burger--open::before {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    content: "";
    background: url("../img/bg/burger.svg") center center no-repeat;
    filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.2));
    background-size: 20px 20px;
  }
}
@media (max-width: 1439px) and (max-width: 767px) {
  .burger--open::before {
    background-size: 15px 15px;
  }
}
@media (max-width: 1439px) {
  .burger--close::before {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    content: "";
    background: url("../img/bg/close.svg") center center no-repeat;
    filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.2));
    background-size: 20px 20px;
  }
}
@media (max-width: 1439px) and (max-width: 767px) {
  .burger--close::before {
    background-size: 15px 15px;
  }
}
@media (max-width: 1439px) {
  .burger--nojs {
    display: none;
  }
}
@media (max-width: 767px) {
  .burger {
    width: 15px;
    height: 30px;
  }
}

.menu {
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
}
@media (max-width: 1439px) {
  .menu {
    width: 100%;
    margin-top: 15px;
  }
}
@media (max-width: 767px) {
  .menu {
    display: none;
  }
}
@media (max-width: 767px) {
  .menu--opened-on-mobile {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 18px;
  }
  .menu--opened-on-mobile .menu__site-nav {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    margin: 0;
  }
  .menu--opened-on-mobile .menu__item {
    margin: 0;
    margin-left: 37px;
  }
  .menu--opened-on-mobile .menu__link {
    padding: 4px 0;
    color: rgba(0, 0, 0, 0.7);
  }
  .menu--opened-on-mobile .menu_phone-link {
    position: relative;
    display: block;
    order: -1;
    margin-top: 4px;
    margin-bottom: 20px;
    margin-left: 37px;
    color: rgba(0, 0, 0, 0.7);
    text-decoration: none;
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 20px;
  }
  .menu--opened-on-mobile .menu_phone-link::before {
    position: absolute;
    top: -1px;
    left: -34px;
    width: 20px;
    height: 20px;
    content: "";
    background: url("../img/bg/phone.svg") center center no-repeat;
    opacity: 0.3;
  }
}

.menu__site-nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 500px;
  padding: 0;
  margin: 0 -15px;
  list-style: none;
}

.menu__item {
  margin: 0 15px;
}

.menu__link {
  display: block;
  padding: 10px 0;
  color: rgba(0, 0, 0, 0.7);
  text-decoration: none;
  transition: 100ms;
}
.menu__link:hover, .menu__link:focus {
  color: rgba(0, 0, 0, 0.4);
}
.menu__link:active {
  color: rgba(0, 0, 0, 0.2);
}

.menu_phone-link {
  display: none;
}

.menu__user-nav {
  display: flex;
  justify-self: flex-end;
}

.user-nav {
  align-items: center;
  justify-content: space-between;
  width: 136px;
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .user-nav--opened-on-mobile {
    position: absolute;
    top: 0;
    right: -1px;
    width: 90px;
  }
  .user-nav--opened-on-mobile .user-nav__item {
    width: 20px;
    height: 20px;
  }
}

.user-nav__item {
  width: 21px;
  height: 21px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.2));
}

.user-nav__icon {
  position: relative;
  width: 100%;
  height: 100%;
  stroke: rgba(0, 0, 0, 0.7);
  transition: 150ms;
}
.user-nav__icon:hover, .user-nav__icon:focus {
  stroke: #da498c;
}
.user-nav__icon:active {
  opacity: 0.3;
}

.search-form {
  position: relative;
  display: flex;
}
.search-form::before {
  position: absolute;
  top: 10px;
  left: 3px;
  width: 24px;
  height: 24px;
  content: "";
  background: url("../img/bg/search.svg") 0 0 no-repeat;
  opacity: 0.6;
}
@media (max-width: 767px) {
  .search-form::before {
    top: 8px;
    width: 18px;
    height: 18px;
    background-size: 18px 18px;
  }
}

.search-form__input {
  width: 290px;
  padding: 10px;
  padding-left: 50px;
  background-color: transparent;
  border: none;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 22px;
}
.search-form__input:focus {
  outline: none;
}
.search-form__input::placeholder {
  color: rgba(0, 0, 0, 0.3);
  transition: 200ms;
}
.search-form__input:hover::placeholder {
  color: rgba(0, 0, 0, 0.6);
}
.search-form__input:focus::placeholder {
  color: black;
}
@media (max-width: 1439px) {
  .search-form__input {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .search-form__input {
    padding-left: 37px;
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 16px;
  }
}

.search-form__go {
  position: relative;
  display: none;
  width: 30px;
  background-color: transparent;
  border: none;
}
@media (max-width: 767px) {
  .search-form__go {
    display: block;
    width: auto;
    padding: 0;
    text-align: right;
    opacity: 0.3;
    transition: 300ms;
  }
}

.search-form__icon {
  width: 12px;
  height: 20px;
  stroke: rgba(0, 0, 0, 0.7);
  transition: 200ms;
}
.search-form__icon:hover, .search-form__icon:focus {
  stroke: #da498c;
}
.search-form__icon:active {
  opacity: 0.5;
}

.search-form__input:focus + .search-form__go {
  display: block;
  opacity: 1;
}

.catalog-nav {
  display: flex;
  flex-wrap: wrap;
  align-self: center;
  padding: 0;
  margin: 0 -15px;
  list-style: none;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 22px;
}
@media (max-width: 1439px) {
  .catalog-nav {
    display: none;
  }
}
@media (max-width: 1439px) {
  .catalog-nav--opened {
    position: absolute;
    top: 75px;
    right: 64px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 250px;
    padding: 20px;
    margin: 0;
    overflow: hidden;
    background-color: #f8f8f8;
    box-shadow: 0 4px 34px rgba(0, 0, 0, 0.1);
  }
}
@media (max-width: 1439px) {
  .catalog-nav--nojs {
    display: flex;
    width: 100%;
    margin-top: 15px;
    background-color: rgba(0, 0, 0, 0.04);
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 13px;
    font-weight: 500;
    line-height: 22px;
  }
  .catalog-nav--nojs .catalog-nav__item::before {
    position: absolute;
    right: -1px;
    bottom: 15px;
    width: 3px;
    height: 3px;
    content: "";
    background-color: rgba(218, 73, 140, 0.3);
    border-radius: 50%;
  }
  .catalog-nav--nojs .catalog-nav__item:last-child::before {
    opacity: 0;
  }
}
@media (max-width: 767px) {
  .catalog-nav--opened-on-mobile {
    position: static;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 18px;
  }
  .catalog-nav--opened-on-mobile .catalog-nav__item {
    padding: 0;
    margin-left: 37px;
  }
  .catalog-nav--opened-on-mobile .catalog-nav__link {
    padding: 4px 0;
  }
}

.catalog-nav__item {
  position: relative;
  padding: 0 15px;
}

.catalog-nav__link {
  display: block;
  padding: 5px 0;
  color: rgba(218, 73, 140, 0.6);
  text-decoration: none;
  transition: all 150ms;
}
.catalog-nav__link:hover, .catalog-nav__link:focus {
  color: rgba(218, 73, 140, 0.85);
}
.catalog-nav__link:active {
  color: rgba(218, 73, 140, 0.3);
}

.page-footer {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.02);
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 20px;
}
@media (max-width: 767px) {
  .page-footer {
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 18px;
  }
}

.page-footer__top {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 40px;
}
@media (max-width: 398px) {
  .page-footer__top {
    justify-content: center;
  }
}

.page-footer__logo {
  margin-top: 30px;
}
@media (max-width: 1439px) {
  .page-footer__logo {
    margin-top: 13px;
  }
}
@media (max-width: 398px) {
  .page-footer__logo {
    margin-bottom: 18px;
  }
}

.page-footer__phone-block {
  display: flex;
  flex-direction: column;
  margin-left: 38px;
}
@media (max-width: 398px) {
  .page-footer__phone-block {
    position: relative;
    right: 12px;
  }
}

.page-footer__button--callback {
  margin-top: 10px;
}
.page-footer__button--callback::before {
  background-image: url("../img/bg/callback.svg");
  transition: 300ms;
}
.page-footer__button--callback:hover::before {
  transform: rotate(45deg);
}
@media (max-width: 1439px) {
  .page-footer__button--callback {
    padding-right: 0;
    padding-left: 0;
  }
  .page-footer__button--callback::before {
    background-image: none;
  }
}

.page-footer__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .page-footer__bottom {
    flex-direction: column;
  }
}

@media (max-width: 1439px) {
  .page-footer__copy {
    max-width: 200px;
  }
}
@media (max-width: 767px) {
  .page-footer__copy {
    max-width: 100%;
    margin: 0;
    text-align: center;
  }
}

.page-footer__privacy {
  margin: 0 -10px;
  text-align: right;
}
@media (max-width: 1439px) {
  .page-footer__privacy {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .page-footer__privacy {
    max-width: 100%;
    margin: 0;
    margin-bottom: 20px;
    text-align: center;
  }
}

.page-footer__privacy a {
  margin: 0 10px;
  color: rgba(0, 0, 0, 0.5);
  text-decoration: none;
  transition: 150ms;
}
.page-footer__privacy a:hover, .page-footer__privacy a:focus {
  color: rgba(0, 0, 0, 0.8);
}
.page-footer__privacy a:active {
  color: rgba(0, 0, 0, 0.4);
}

.footer-nav {
  column-count: 2;
  column-gap: 90px;
  padding: 0;
  margin: 0;
  vertical-align: top;
  list-style: none;
}
@media (max-width: 1439px) {
  .footer-nav {
    column-count: 1;
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 16px;
  }
}
@media (max-width: 767px) {
  .footer-nav {
    display: none;
  }
}

.footer-nav__item {
  padding-top: 10px;
}
@media (max-width: 1439px) {
  .footer-nav__item {
    padding-top: 5px;
  }
}

.footer-nav__link {
  display: block;
  padding-top: 5px;
  color: rgba(0, 0, 0, 0.6);
  text-decoration: none;
  transition: 150ms;
}
.footer-nav__link:hover, .footer-nav__link:focus {
  color: black;
}
.footer-nav__link:active {
  color: rgba(0, 0, 0, 0.4);
}

.phone-list {
  position: relative;
  padding-top: 13px;
  margin: 0;
  color: rgba(0, 0, 0, 0.7);
}

.phone-list__item {
  margin-bottom: 20px;
}

.phone-list__number {
  color: rgba(0, 0, 0, 0.6);
  text-decoration: none;
  transition: 150ms;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 28px;
  font-weight: 500;
  line-height: 32px;
}
.phone-list__number:hover, .phone-list__number:focus {
  opacity: 0.4;
}
.phone-list__number:active {
  opacity: 0.2;
}
