@media (max-width: 1439px) {
  .services__list {
    grid-template-rows: minmax(170px, 1fr) minmax(170px, 1fr);
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
}

.service-card {
  padding: 25px 25px;
  background-color: rgba(0, 0, 0, 0.04);
  border-radius: 12px;
  transition: 500ms;
}
.service-card--big {
  grid-row: span 2;
}
.service-card:hover, .service-card:focus {
  background-color: rgba(218, 73, 140, 0.85);
}
.service-card--exlusive {
  background: rgba(0, 0, 0, 0.04) url("../img/bg/services-icons/exlusive.svg") 140% 130% no-repeat;
  background-size: 250px auto;
}
.service-card--discount {
  background: rgba(0, 0, 0, 0.04) url("../img/bg/services-icons/discount.svg") 110% 25px no-repeat;
  background-size: 150px auto;
}
.service-card--support {
  background: rgba(0, 0, 0, 0.04) url("../img/bg/services-icons/support.svg") 106% 35px no-repeat;
  background-size: 140px auto;
}
.service-card--franchising {
  background: rgba(0, 0, 0, 0.04) url("../img/bg/services-icons/franchising.svg") 120% 30px no-repeat;
  background-size: 140px auto;
}
.service-card--tradein {
  background: rgba(0, 0, 0, 0.04) url("../img/bg/services-icons/tradein.svg") 107% 30px no-repeat;
  background-size: 130px auto;
}
.service-card--delivery {
  background: rgba(0, 0, 0, 0.04) url("../img/bg/services-icons/delivery.svg") 125% 20px no-repeat;
  background-size: 150px auto;
}
.service-card--testdrive {
  background: rgba(0, 0, 0, 0.04) url("../img/bg/services-icons/testdrive.svg") 103% 35px no-repeat;
  background-size: 135px auto;
}

.service-card__link {
  display: block;
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0.8);
  text-decoration: none;
}

.service-card__title {
  margin: 0;
  margin-bottom: 15px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 30px;
}
.service-card--big .service-card__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 28px;
  font-weight: 700;
  line-height: 34px;
}

.service-card__description {
  width: 70%;
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;
}
.service-card--big .service-card__description {
  width: 100%;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 22px;
  font-weight: 300;
  line-height: 30px;
}

.map__title {
  margin-bottom: 20px;
}

.map__inner {
  position: relative;
}

.map-item--elekrtostal {
  display: none;
}

.map__address {
  position: absolute;
  top: 20px;
  left: 70px;
}
@media (max-width: 767px) {
  .map__address {
    position: static;
  }
}

.address {
  width: auto;
  height: auto;
  padding: 25px;
  white-space: nowrap;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 34px rgba(0, 0, 0, 0.08);
}
@media (max-width: 767px) {
  .address {
    padding: 0;
    margin-bottom: 40px;
    box-shadow: none;
  }
}

.address__list {
  padding: 0;
  margin: 0;
  list-style: none;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
}
@media (max-width: 767px) {
  .address__list {
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
  }
}

.address__item {
  position: relative;
  border-radius: 10px;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.06);
}

.address__label {
  display: block;
  padding: 20px;
  padding-left: 40px;
  margin-top: 20px;
  cursor: pointer;
  border-radius: 10px 10px 0 0;
  transition: 200ms linear;
}
.address__input:checked + .address__label {
  background-color: rgba(218, 73, 140, 0.3);
}
.address__label--margin {
  margin-top: 0;
}
@media (max-width: 767px) {
  .address__label {
    margin-top: 15px;
  }
}

.address__arrow {
  position: absolute;
  top: 23px;
  left: 18px;
  stroke: rgba(0, 0, 0, 0.2);
  transition: 200ms;
}
.address__input:checked ~ .address__arrow {
  stroke: #da498c;
  transform: rotate(90deg);
}

.address__input {
  display: none;
}

.address__schedule {
  max-height: 0;
  overflow: hidden;
  transition: max-height 600ms linear;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 27px;
}
.address__input:checked ~ .address__schedule {
  max-height: 100px;
}
@media (max-width: 767px) {
  .address__input:checked ~ .address__schedule {
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 12px;
    font-weight: 300;
    line-height: 18px;
  }
}

.address__text {
  padding: 10px;
  padding-left: 40px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 18px;
}
@media (max-width: 767px) {
  .address__text {
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 12px;
    font-weight: 300;
    line-height: 18px;
  }
}